.item {
  display: flex;
  background-color: rgba(253, 255, 255, 0.31);
  border-radius: 30px;
  box-shadow: rgb(0 0 0 / 5%) 0px 6px 24px 0px, rgba(253, 255, 255, 0.31);
  margin-top: 15px;
  margin-right:30px ;
  margin-left:20px ;
  padding: 15px;
}

.itemImg img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  object-fit: cover;
  border: 1px ridge #000101;
}

.itemText {
  font-size: 18px;
  color: #000101;
  margin-left: 10px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 5px 0;
}

.itemName {
  margin-left: 10px;
  font-size: .9rem;
  color: #000101;
}

.itemLike {
  align-self: flex-end;
  float: right;

  padding: 5px 10px;
  border-radius: 8px;
  color: #cb0529;

}

.itemLike:hover {
  cursor: pointer;
  color: #d79f9f;
}